import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { DeliveriesTimes } from '@core/interfaces/deliveries-times-interface';

@Component({
  selector: 'app-delivery-times-form',
  templateUrl: './delivery-times-form.component.html',
  styleUrls: ['./delivery-times-form.component.scss'],
})
export class DeliveryTimesFormComponent implements OnInit {
  @Input() deliveries: DeliveriesTimes[] = [];
  @Input() deliveryTimeId: number = 0;
  @Output() submitForm = new EventEmitter();
  @Output() closeForm = new EventEmitter();

  readonly form: FormGroup = new FormGroup({});
  private inputDelivery: FormControl = new FormControl();

  constructor() {}

  ngOnInit(): void {
    this.initForm();
  }

  initForm() {
    this.inputDelivery = new FormControl(null, [Validators.required]);
    this.addControlls();
  }

  addControlls() {
    Object.keys(this.form.controls).forEach((control) => {
      this.form.removeControl(control);
    });
    this.form.addControl('inputDelivery', this.inputDelivery);
    this.changeDelivery(this.deliveryTimeId);
  }

  changeDelivery(deliveryId: number) {
    this.form.get('inputDelivery')!.setValue(deliveryId);
  }

  updateDelivery() {
    let deliveryId: number = +this.form.get('inputDelivery')!.value;
    this.submitForm.emit(deliveryId);
  }

  close() {
    this.closeForm.emit(false);
  }
}
